<script>
import {mapState} from "vuex";

export default {
  data() {
    return {
      name: '',
      calendarUrl: '',
      studentsHeaders: [
        {text: 'Name', value: 'name'},
        {text: 'Actions', value: 'actions', sortable: false}
      ],
      dialog: true,
      loading: false,
    }
  },
  computed: {
    ...mapState(['currentGroup']),
    students() {
      return this.currentGroup.Students;
    }
  },
  mounted() {
    this.$store.dispatch('fetchGroupById', Number(this.$route.params.groupId)).then(() => {
      this.name = this.currentGroup.name;
      this.calendarUrl = this.currentGroup.calendarUrl;
    });
  },
  methods: {
    closeModal() {
      this.dialog = false;
      this.$router.push('/group');
    },
    saveGroup() {
      this.loading = true;
      this.$store.dispatch('editGroup', {
        id: this.$route.params.groupId,
        name: this.name,
        calendarUrl: this.calendarUrl
      }).then(() => {
        this.loading = false;
        this.closeModal();
      })
    }
  }
}
</script>

<template>
  <v-container>
    <router-view></router-view>
    <v-card flat>
      <v-card-title>
        <span class="headline">Edit Group</span>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-text-field
              label="Name"
              required
              v-model="name"
              :disabled="loading"
          ></v-text-field>
          <v-text-field
              label="ADE Calendar URL"
              required
              v-model="calendarUrl"
              :disabled="loading"
          ></v-text-field>
          <v-data-table
              :headers="studentsHeaders"
              :items="students"
              item-key="id"
              class="elevation-1"
              sort-by="name"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title>Students</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn
                    color="blue darken-1"
                    text
                    @click="$router.push({name: 'GroupAddStudent', params: {groupId: $route.params.groupId}})"
                >Add Student
                </v-btn>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">

              <v-icon @click="$router.push({name: 'GroupEditStudent', params: {groupId: $route.params.groupId, studentId: item.id}})">
                mdi-pencil
              </v-icon>
              <v-icon @click="$router.push({name: 'GroupUnlinkStudent', params: {groupId: $route.params.groupId, studentId: item.id}})">
                mdi-link-off
              </v-icon>
              <v-icon @click="$router.push({name: 'GroupMoveStudent', params: {groupId: $route.params.groupId, studentId: item.id}})">
                mdi-transfer-right
              </v-icon>
              <v-icon @click="$router.push({name: 'GroupDeleteStudent', params: {groupId: $route.params.groupId, studentId: item.id}})">
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="blue darken-1"
            text
            @click="closeModal"
        >Cancel
        </v-btn>
        <v-btn
            color="blue darken-1"
            text
            @click="saveGroup"
            :loading="loading"
        >Save
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

